nav {
  width: 100%;
  height: 5.938rem;
  margin: 0 0 2.375rem;
  padding: 1.438rem 6.188rem 1.688rem 6.25rem;
  background-color: white;
  display: flex;
  justify-content: end;
}

nav > ul {
  width: 40%;
  height: 100%;
  display: flex;
}

nav li {
  width: 3.688rem;
  height: 1rem;
  margin: 1.125rem 2.563rem 0.688rem 0;
  list-style: none;
}

nav li a {
  font-family: 'Montserrat', sans-serif;
  font-size: 0.99rem;
  letter-spacing: 1.9px;
  color: var(--black-two);
  text-decoration: none;
}

nav > h1 {
  width: 18rem;
  height: 2.313rem;
  margin: 0.375rem 2.938rem 0.125rem 0;
  font-family: 'Montserrat', sans-serif;
  font-size: 1.875rem;
  font-weight: bold;
  letter-spacing: normal;
  color: var(--azure);
}

.userIcon {
  width: 2.813rem;
  height: 2.813rem;
  margin: 0 0 0 15.5rem;
  padding: 0.875rem;
  border: solid 1px var(--grey-border);
  border-radius: 50%;
}

.fa-user-alt {
  color: var(--azure);
}
